	import utils from '@/utils/utils.js';
export default {

	data() {
		return {
			phone:"",
			phonee:"",
			countdown:60,
		}
	},
	methods: {
		updatesave(){
			if(this.phone==''){
				uni.showToast({
					title:"请输入手机号"
				})
				return;
			}
			if(!utils.checkMobile(this.phone) ){
				uni.showToast({
					title:"请输入正确的手机号码",
				})
				return;
			}
			if(this.phonee==''){
				uni.showToast({
					title:"请再次输入手机号"
				})
					return;
			}
			if(this.phone != this.phonee){
				uni.showToast({
					title:"两次手机号输入不一致"
				})
					return;
			}
			if(this.phone == this.phonee){
				uni.showToast({
					title:"修改成功",
				})
				uni.reLaunch({
					url:"../login/login",
				})
					return;
			}
		},
		numberst(e){
					//其他代码....
					this.countDown();
				},
				// 倒计时
						countDown(){
							let self = this;
							self.countdown = 60;
							self.countdown -= 1;
							if(self.clear){
								clearInterval(self.clear)
							}
							self.clear = null;
							self.clear = setInterval(_ => {
								if(self.countdown > 0){
									self.countdown -= 1;
								}else{
									clearInterval(self.clear)
								}
							},1000)
						},
	}
}
